.BlogContainer {
  width: 100vw;
  height: 500vh;
  overflow: hidden;
  margin-top: 10vh;
  margin-bottom: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .BlogContainer {
    height: 400vh;
  }
}
.BlogContainer__element {
  width: 105vh;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
@media screen and (orientation: portrait) {
  .BlogContainer__element {
    width: 90vw;
    height: auto;
  }
}
.BlogContainer__element__image {
  height: 65vh;
  cursor: pointer;
  transition: filter 450ms;
  will-change: filter;
}
@media screen and (orientation: portrait) {
  .BlogContainer__element__image {
    height: auto;
    width: 90vw;
  }
}
.BlogContainer__element__image:hover {
  filter: brightness(115%);
}
.BlogContainer__element__title {
  transform: translateX(4vw);
  font-size: 3.5vh;
  font-weight: 400;
  margin-top: 3vh;
}
@media screen and (orientation: portrait) {
  .BlogContainer__element__title {
    font-size: 3vh;
  }
}
.BlogContainer__element__text {
  transform: translateX(4vw);
  color: gray;
  font-weight: 400;
  max-width: 45vw;
  font-size: 1.9vh;
  margin-top: 2vh;
}
@media screen and (orientation: portrait) {
  .BlogContainer__element__text {
    max-width: 80vw;
  }
}
.BlogContainer__element__date {
  transform: translateX(4vw);
  color: rgb(90, 90, 90);
  font-weight: 300;
  font-size: 1.7vh;
  margin-top: 2vh;
}