.TeamMember {
  width: 37vh;
  height: 56vh;
  margin-top: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .TeamMember {
    width: 37vh;
    height: 56vh;
  }
}
.TeamMember__blankImage {
  width: 100%;
  height: 84%;
  background-color: rgb(241, 241, 241);
  display: block;
}
.TeamMember__name {
  font-size: 2.6vh;
  font-weight: 400;
  color: black;
  margin: 0;
  display: block;
}
.TeamMember__position {
  font-size: 1.8vh;
  font-weight: 300;
  color: gray;
  display: block;
}