.Article {
  width: 40vh;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1vh;
  padding-top: 4.5vh;
  padding-bottom: 4.5vh;
}
@media screen and (orientation: portrait) {
  .Article {
    width: 95vw;
    padding-left: 3vh;
    height: auto;
  }
}
.Article__title {
  font-size: 2.8vh;
  font-weight: 500;
  cursor: pointer;
  transition: color 250ms;
  will-change: color;
}
@media screen and (orientation: portrait) {
  .Article__title {
    font-size: 3vh;
  }
}
.Article__title:hover {
  color: #555;
}
.Article__date {
  color: rgb(167, 167, 167);
  font-weight: 500;
  font-size: 1.5vh;
  margin-top: 1vh;
  margin-bottom: 1vh;
}
.Article__text {
  font-size: 1.8vh;
  color: rgb(141, 141, 141);
  font-weight: 400;
  max-width: 95%;
}