.Group {
  width: 100vw;
  height: auto;
  margin-top: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media screen and (orientation: portrait) {
  .Group {
    height: 110vh;
  }
}
.Group__text {
  text-align: center;
  width: 50vw;
  max-width: 50vw;
  margin-left: 25vw;
  font-size: 2vh;
  font-weight: 300;
  color: rgb(110, 110, 110);
  margin-top: 5vh;
}
@media screen and (orientation: portrait) {
  .Group__text {
    width: 90vw;
    max-width: 90vw;
    margin-left: 5vw;
    font-size: 1.9vh;
  }
}