.HeroTitle {
  text-align: center;
  color: white;
  font-size: 7vh;
  font-weight: 500;
  display: block;
  position: absolute;
  z-index: 1000;
  width: 95vw;
  max-width: 95vw;
  margin-top: 25vh;
}
@media screen and (orientation: portrait) {
  .HeroTitle {
    font-size: 3.3vh;
    max-width: 90vw;
    width: 90vw;
    margin-left: 2.5vw;
  }
}

.HeroSubtitle {
  text-align: center;
  color: rgb(173, 173, 173);
  font-size: 2.25vh;
  font-weight: 400;
  display: block;
  position: absolute;
  z-index: 1000;
  width: 95vw;
  max-width: 95vw;
  margin-top: 35vh;
}
@media screen and (orientation: portrait) {
  .HeroSubtitle {
    margin-top: 33.5vh;
    max-width: 90vw;
    width: 90vw;
    margin-left: 2.5vw;
  }
}

.HeroButton {
  width: 10vw;
  height: 6vh;
  display: block;
  position: absolute;
  z-index: 1000;
  margin-left: 42.5vw;
  margin-top: 42.5vh;
  cursor: pointer;
  border: none;
  color: black;
  background-color: white;
  font-size: 1.65vh;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .HeroButton {
    width: 35vw;
    margin-left: 30vw;
    margin-top: 43.5vh;
  }
}

.HeroButton:hover {
  background-color: whitesmoke;
}