.NewsWrapper {
  width: 100vw;
  height: auto;
}
@media screen and (orientation: portrait) {
  .NewsWrapper {
    height: 150vh;
  }
}
.NewsWrapper__ArticleContainer {
  width: 80vw;
  margin-top: 2.5vh;
  margin-left: 10vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .NewsWrapper__ArticleContainer {
    width: 95vw;
    margin-left: 2.5vw;
    flex-direction: column;
  }
}