.Projects {
  width: 100vw;
  height: 280vh;
  overflow-x: hidden;
}
@media screen and (orientation: portrait) {
  .Projects {
    height: 175vh;
  }
}
.Projects__elements {
  height: 260vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
@media screen and (orientation: portrait) {
  .Projects__elements {
    height: 150vh;
  }
}
.Projects__elements__el {
  width: 90vw;
  height: 66vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .Projects__elements__el {
    height: 50vh;
  }
}
.Projects__elements__el__img {
  width: 70%;
  height: 55vh;
  margin-left: 15%;
  cursor: pointer;
  transition: filter 250ms;
  will-change: filter;
}
@media screen and (orientation: portrait) {
  .Projects__elements__el__img {
    width: 90%;
    height: 40vh;
    margin-left: 5%;
  }
}
.Projects__elements__el__img:hover {
  filter: brightness(125%);
}
.Projects__elements__el__title {
  text-align: center;
  font-size: 2.75vh;
  font-weight: 400;
}
.Projects__elements__el__subtitle {
  text-align: center;
  font-size: 2.15vh;
  font-weight: 300;
  color: gray;
}