.GrowWrapper {
  width: 95vw;
  margin-left: 2.5vw;
  height: 40vh;
  background-color: whitesmoke;
  padding-top: 5vh;
  padding-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.GrowWrapper__GrowSubtitle {
  text-align: center;
  font-size: 2.25vh;
  max-width: 50vw;
  width: 50vw;
  margin-left: 22.5vw;
  font-weight: 400;
  color: gray;
}
@media screen and (orientation: portrait) {
  .GrowWrapper__GrowSubtitle {
    width: 85vw;
    max-width: 85vw;
    margin-left: 5vw;
    font-size: 2.1vh;
  }
}
.GrowWrapper__button {
  width: 10vw;
  height: 5vh;
  font-size: 1.75vh;
  border: none;
  color: white;
  background-color: #222;
  align-self: center;
  justify-self: center;
  cursor: pointer;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .GrowWrapper__button {
    width: 18vh;
  }
}
.GrowWrapper__button:hover {
  background-color: #444;
}