.Form {
  width: 40vw;
  height: auto;
  border: solid rgb(226, 226, 226) 0.1vh;
  padding: 3vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
}
@media screen and (orientation: portrait) {
  .Form {
    width: 90vw;
  }
}
.Form__title {
  color: black;
  font-weight: 400;
  font-size: 4.2vh;
}
.Form__description {
  color: gray;
  font-weight: 400;
  font-size: 1.75vh;
  padding-right: 2vh;
  margin-top: 2vh;
  margin-bottom: 2vh;
}
.Form__button {
  width: 100%;
  height: 5.5vh;
  margin-top: 1.5vh;
  cursor: pointer;
  border: none;
  color: white;
  background-color: #222;
  font-size: 1.6vh;
  transition: background-color 250ms;
  will-change: background-color;
}
.Form__button:hover {
  background-color: #444;
}
.Form__inputElement {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  height: auto;
  width: auto;
}
.Form__inputElement__label {
  color: black;
  text-transform: uppercase;
  font-size: 1.5vh;
  font-weight: 500;
  margin-bottom: 1.5vh;
  transform: translateY(2vh);
}
.Form__inputElement__input {
  height: 5.5vh;
  width: 100%;
  padding-left: 2vh;
  font-size: 1.6vh;
  outline: none;
  border: solid rgb(182, 182, 182) 0.1vh;
  margin-top: 1.5vh;
  transition: border-color 250ms;
  will-change: border-color;
}
.Form__inputElement__input:focus {
  border-color: black;
}
.Form__inputElement__textarea {
  padding-top: 2vh;
  max-width: 100%;
  min-width: 100%;
  max-height: 24vh;
  min-height: 24vh;
}
.Form__inputElement__input::placeholder {
  color: gray;
}

.Info {
  height: 85vh;
  width: 20vw;
  transform: translateX(-10vw);
}
@media screen and (orientation: portrait) {
  .Info {
    transform: translateX(0vw);
    width: 90vw;
    text-align: center;
    height: 50vh;
  }
}
.Info__title {
  color: black;
  font-weight: 500;
  font-size: 1.8vh;
  margin-top: 2vh;
}
.Info__text {
  color: gray;
  font-weight: 400;
  font-size: 2.1vh;
  margin-top: 1.5vh;
}