.WhatArticle {
  width: 120vh;
  height: 50vh;
  margin-top: 10vh;
  margin-left: 1vh;
  margin-right: 1vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
}
@media screen and (orientation: portrait) {
  .WhatArticle {
    width: 95vw;
    margin-left: 2.5vw;
    flex-direction: column-reverse;
    height: 1200vh;
    overflow: hidden;
  }
}
.WhatArticle__first {
  width: 35%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  padding-top: 6vh;
  padding-bottom: 6vh;
}
@media screen and (orientation: portrait) {
  .WhatArticle__first {
    width: 100%;
    height: auto;
    align-items: center;
    overflow: hidden;
  }
}
.WhatArticle__first__title {
  color: #8f8f8f;
  font-size: 1.6vh;
  font-weight: 500;
  text-transform: uppercase;
}
@media screen and (orientation: portrait) {
  .WhatArticle__first__title {
    transform: translateY(2vh);
    overflow: hidden;
    height: 3vh;
  }
}
.WhatArticle__first__title2 {
  font-size: 4vh;
  font-weight: 400;
}
@media screen and (orientation: portrait) {
  .WhatArticle__first__title2 {
    transform: translateY(1vh);
    margin-top: 1vh;
    margin-bottom: 1vh;
    overflow: hidden;
    height: 6vh;
  }
}
.WhatArticle__first__description {
  font-size: 1.9vh;
  color: gray;
  font-weight: 300;
  max-width: 90%;
}
@media screen and (orientation: portrait) {
  .WhatArticle__first__description {
    text-align: center;
    max-width: 98%;
    height: 15vh;
    transform: translateY(1vh);
    overflow: hidden;
  }
}
.WhatArticle__first__button {
  cursor: pointer;
  color: white;
  background-color: #222;
  border: none;
  width: 10vw;
  height: 5vh;
  font-size: 1.6vh;
  transition: background-color 250ms;
  will-change: background-color;
}
@media screen and (orientation: portrait) {
  .WhatArticle__first__button {
    width: 15vh;
    height: 5vh;
    padding-top: 1vh;
    padding-bottom: 1vh;
    margin-top: 1vh;
    transform: translateY(1.5vh);
    overflow: hidden;
  }
}
.WhatArticle__first__button:hover {
  background-color: #333;
}
.WhatArticle .reverseFlex {
  justify-content: flex-start;
}
.WhatArticle__second {
  width: auto;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .WhatArticle__second {
    width: 100%;
    height: 45%;
    overflow: hidden;
  }
}
.WhatArticle__second__image {
  height: 20vw;
  display: block;
}
@media screen and (orientation: portrait) {
  .WhatArticle__second__image {
    width: 95vw;
    height: 27.5vh;
    overflow: hidden;
    object-fit: fill;
  }
}

.reverse {
  flex-direction: row-reverse;
}
@media screen and (orientation: portrait) {
  .reverse {
    flex-direction: column-reverse;
  }
}