.Home,
.Work,
.Team,
.Blog,
.Contact,
.Project,
.BlogPost {
  width: 100vw;
  overflow-x: hidden;
  height: auto;
}
.Home__post,
.Work__post,
.Team__post,
.Blog__post,
.Contact__post,
.Project__post,
.BlogPost__post {
  width: 100vw;
  height: 195vh;
  padding-top: 5vh;
  padding-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 20vw;
  padding-right: 20vw;
}
@media screen and (orientation: portrait) {
  .Home__post,
  .Work__post,
  .Team__post,
  .Blog__post,
  .Contact__post,
  .Project__post,
  .BlogPost__post {
    padding-left: 2.5vw;
    padding-right: 2.5vw;
  }
}
.Home__post__title,
.Work__post__title,
.Team__post__title,
.Blog__post__title,
.Contact__post__title,
.Project__post__title,
.BlogPost__post__title {
  color: black;
  font-size: 4vh;
  font-weight: 500;
  padding-right: 25vw;
}
.Home__post__date,
.Work__post__date,
.Team__post__date,
.Blog__post__date,
.Contact__post__date,
.Project__post__date,
.BlogPost__post__date {
  color: gray;
  font-weight: 400;
  font-size: 2vh;
}
.Home__post__title2,
.Work__post__title2,
.Team__post__title2,
.Blog__post__title2,
.Contact__post__title2,
.Project__post__title2,
.BlogPost__post__title2 {
  color: black;
  font-size: 3.6vh;
  font-weight: 500;
  padding-left: 5vw;
  padding-right: 5vw;
}
.Home__post__subtitle,
.Work__post__subtitle,
.Team__post__subtitle,
.Blog__post__subtitle,
.Contact__post__subtitle,
.Project__post__subtitle,
.BlogPost__post__subtitle {
  color: black;
  font-size: 2.7vh;
  font-weight: 500;
  padding-left: 5vw;
  padding-right: 5vw;
}
.Home__post__paragraph,
.Work__post__paragraph,
.Team__post__paragraph,
.Blog__post__paragraph,
.Contact__post__paragraph,
.Project__post__paragraph,
.BlogPost__post__paragraph {
  font-size: 1.85vh;
  color: #888;
  font-weight: 400;
  padding-left: 5vw;
  padding-right: 5vw;
}
.Home__post__image,
.Work__post__image,
.Team__post__image,
.Blog__post__image,
.Contact__post__image,
.Project__post__image,
.BlogPost__post__image {
  width: 100%;
}
.Home__mainContent,
.Work__mainContent,
.Team__mainContent,
.Blog__mainContent,
.Contact__mainContent,
.Project__mainContent,
.BlogPost__mainContent {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .Home__mainContent,
  .Work__mainContent,
  .Team__mainContent,
  .Blog__mainContent,
  .Contact__mainContent,
  .Project__mainContent,
  .BlogPost__mainContent {
    height: 180vh;
    flex-direction: column;
  }
}
.Home__Container,
.Work__Container,
.Team__Container,
.Blog__Container,
.Contact__Container,
.Project__Container,
.BlogPost__Container {
  width: 70vw;
  margin-left: 15vw;
  margin-top: 9vh;
  margin-bottom: 9vh;
  height: auto;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
@media screen and (orientation: portrait) {
  .Home__Container,
  .Work__Container,
  .Team__Container,
  .Blog__Container,
  .Contact__Container,
  .Project__Container,
  .BlogPost__Container {
    width: 100vw;
    margin-left: 0;
    height: auto;
    flex-wrap: nowrap;
    flex-direction: column;
  }
}