.ProjectContent {
  width: 100vw;
  height: 270vh;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media screen and (orientation: portrait) {
  .ProjectContent {
    height: 180vh;
  }
}
.ProjectContent__top {
  width: 100vw;
  height: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20vw;
  padding-right: 20vw;
}
@media screen and (orientation: portrait) {
  .ProjectContent__top {
    flex-direction: column;
    text-align: center;
    height: 90vh;
    padding-left: 5vw;
    padding-right: 5vw;
  }
}
.ProjectContent__top__left {
  margin-right: 3vw;
  width: 30%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .ProjectContent__top__left {
    width: 100%;
    align-items: center;
    height: 28vh;
  }
}
.ProjectContent__top__left__section {
  height: auto;
}
.ProjectContent__top__left__section__title {
  color: black;
  font-size: 1.55vh;
  font-weight: 500;
  display: block;
  margin-top: 1vh;
}
.ProjectContent__top__left__section__text {
  margin-top: 1vh;
  font-weight: 400;
  color: gray;
  font-size: 1.6vh;
}
.ProjectContent__top__right {
  margin-left: 3vw;
  width: 70%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .ProjectContent__top__right {
    width: 100%;
    height: 50vh;
  }
}
.ProjectContent__top__right__paragraph {
  font-size: 1.7vh;
  color: gray;
  font-weight: 400;
  max-width: 100%;
  margin-top: 1vh;
}
@media screen and (orientation: portrait) {
  .ProjectContent__top__right__paragraph {
    font-size: 1.6vh;
  }
}
.ProjectContent__bottom {
  height: 165vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .ProjectContent__bottom {
    height: 70vh;
  }
}
.ProjectContent__bottom__image {
  margin-top: 1vh;
  height: 45vh;
}
@media screen and (orientation: portrait) {
  .ProjectContent__bottom__image {
    height: auto;
    width: 90vw;
  }
}