.Services {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
@media screen and (orientation: portrait) {
  .Services {
    height: 180vh;
  }
}
.Services__container {
  width: 60vw;
  height: 70vh;
  margin-left: 20vw;
  margin-top: 2.5vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
@media screen and (orientation: portrait) {
  .Services__container {
    width: 90vw;
    height: 150vh;
    margin-top: 10vh;
    margin-left: 5vw;
    flex-direction: column;
    flex-wrap: nowrap;
  }
}
.Services__container__element {
  width: 18vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
@media screen and (orientation: portrait) {
  .Services__container__element {
    width: 90vw;
    height: auto;
    align-items: center;
  }
}
.Services__container__element__image {
  width: 5vh;
}
.Services__container__element__title {
  font-size: 2.5vh;
  font-weight: 400;
  margin-top: 2vh;
  margin-bottom: 2vh;
  overflow: hidden;
}
@media screen and (orientation: portrait) {
  .Services__container__element__title {
    height: 4vh;
  }
}
.Services__container__element__subtitle {
  font-size: 1.9vh;
  font-weight: 400;
  max-width: 18vw;
  width: 18vw;
  height: 50%;
  color: gray;
}
@media screen and (orientation: portrait) {
  .Services__container__element__subtitle {
    width: 85vw;
    max-width: 85vw;
    text-align: center;
  }
}